// Styleguide: Signature
.sig {
  align-items: baseline;
  border-top: 1px dotted $color-alpha;
  background-color: $color-psi;
  background-image: linear-gradient(to right, $color-psi, $color-chi, $color-psi);
  color: $color-omega;
  display: flex;
  justify-content: center;
  line-height: 1;
  padding: 0 rem(10px) rem(6px);

  p {
    font-size: ms(-1);
    margin: 0;
  }
}

.sig__link {
  align-items: baseline;
  display: inline-flex;
  color: $color-omega;
  text-decoration: none;
  transition: all $trd $tra;

  svg {
    height: rem(20px);
    margin-right: rem(2px);
    transform: translateY(rem(4px));
    transform-origin: center;
    transition: all $trd $tra forwards;
    width: rem(20px);
  }
}
